const SERVER_DOMAIN = `http://localhost:${3300}`;

const createAPI = (text) => {
    fetch(`${SERVER_DOMAIN}/api`, {
        method: "POST",
        body: JSON.stringify({
            data: text,
        })
    }).then(res => console.log(res))
}

const readAPI = () => {
    fetch(`${SERVER_DOMAIN}/api`, {
        method: "GET",
    }).then(res => console.log(res))
}


const updateAPI = (text) => {
    fetch(`${SERVER_DOMAIN}/api`, {
        method: "PUT",
        body: {
            data: text,
        },
    }).then(res => console.log(res))
}

const deleteAPI = () => {
    fetch(`${SERVER_DOMAIN}/api`, {
        method: "DELETE",
    }).then(res => console.log(res))
}
export { createAPI, readAPI, updateAPI, deleteAPI };